:root {
  --primary-color: #170394;
  --accent-color: #dec105;
  --border-color: #777;
}

.clear {
  clear: both;
}

.App {
  text-align: center;
  width: 100%;
  margin: auto;
  max-width: 1500px;
  min-width: 625px;
  background: #fff;
  min-height: 500px;
  border-left: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  font-family: 'helvetica';
  box-sizing: border-box;
}

.header {
  min-width: 625px;
  background: var(--primary-color);
}

.header a {
  color: var(--accent-color);
  text-decoration: none;
}

#header-menu {
  max-width: 1500px;
  width: 100%;
  margin: auto;
  list-style-type: none;
  font-size: 2rem;
  padding: 0px;
}

#header-menu li {
  display: inline;
  padding: 10px;
}